import { Row } from "@nextui-org/react";
import BlockTitle from "../../MainPage/BlockTitle";
import FilterController from "../../Common/FilterController/FilterController";
import useFetch from "../../../hooks/useFetch";
import { API } from "../../../api/api";
import ShowItem from "../Shows/ShowItem/ShowItem";

function ArchivePage() {
    const shows = useFetch({initial: [], api: API.currentShows})

    return (
        <>
            <Row align="center">
                    <BlockTitle color={"tblack"}></BlockTitle>
                    <FilterController/> 
            </Row>
            {shows.data.map((show, idx, arr) => (<ShowItem show={show} isLast={idx === arr.length - 1} isArchive/>))}
        </>
    )
}

export default ArchivePage;