import {Navbar} from "@nextui-org/react";
import logo from '../../images/SimpleLogoWithoutBackground.svg'
import TText from "../Common/TText";
import TCollapse from "../Common/TCollapse";
import { Link, useLocation } from "react-router-dom";
import logoOnlyText from "./logoOnlyText.svg";
import useValue from "../../hooks/useValue";
import { useCallback, useEffect, useRef } from "react";


function MobileHeader() {
    const titleFontCss = {
        fontSize: "27px",
        weight: 500,
        cursor: "pointer"
    };

    const clickable = {
        fontSize: "25px",
        cursor: "pointer",
        padding: "5px 10px",
        "&:hover": {
            border: "1px solid",
        }
    }

    const toggleRef = useRef(null);
    const navbarIsOpen = useValue(false);
    const location = useLocation();

    const onSelectItem = useCallback(() => {
        if (!navbarIsOpen.value) {
            return;
        }
        toggleRef.current.click();
    }, [navbarIsOpen.value])

    useEffect(() => {
        onSelectItem();
    }, [location.pathname, onSelectItem])

    const collapseItems = [
        <Link to={"/pages/poster"} onClick={onSelectItem}>
            <TText light css={titleFontCss}></TText>
        </Link>,
        <TCollapse
            title={<TText light css={titleFontCss}></TText>}
            content={
            <ul style={{margin: "0"}}>
                <Link to="/pages/current-shows" onClick={onSelectItem}>
                    <li><TText light css={clickable}></TText></li>
                </Link>
                <Link to="/pages/pushkin">
                    <li><TText light css={clickable}>  </TText></li>
                </Link>
                <Link to="/pages/archive">
                    <li><TText light css={clickable}></TText></li>
                </Link>
            </ul>}
        />,
        <TCollapse
            title={<TText light css={titleFontCss}> </TText>}
            content={
            <ul style={{margin: "0"}}>
                <Link to="/pages/employees" onClick={onSelectItem}>
                    <li><TText light css={clickable}></TText></li>
                </Link>
                <Link to="/pages/history" onClick={onSelectItem}>
                    <li><TText light css={clickable}></TText></li>
                </Link>
            </ul>}
        />,
        <Link to={"/pages/faq"} onClick={onSelectItem}>
            <TText light css={titleFontCss}>  </TText>
        </Link>,
        <Link to={"/pages/news"} onClick={onSelectItem}>
            <TText light css={titleFontCss}></TText>
        </Link>
    ];

    return (
        <Navbar disableShadow>
            <Navbar.Brand>
                <Link to="/" onClick={onSelectItem}>
                    <img src={logo} width={"30px"}/>
                </Link>
            </Navbar.Brand>
            <Navbar.Content>
                <Link to="/" onClick={onSelectItem}>
                    <img src={logoOnlyText} style={{height: "45px"}}/>
                </Link>
            </Navbar.Content>
            <Navbar.Toggle ref={toggleRef} showIn={"xl"} onChange={isOpen => navbarIsOpen.set(isOpen)}/>
            <Navbar.Collapse>
                {collapseItems.map((item, index) => (
                    <Navbar.CollapseItem
                        key={'header-' + index}
                    >
                        {item}
                    </Navbar.CollapseItem>
                ))}
            </Navbar.Collapse>
        </Navbar>
    )
}

export default MobileHeader;