import { Col, Popover, Row } from "@nextui-org/react";
import { IconChevronDown } from "@tabler/icons-react";
import TText from "../TText";
import useValue from "../../../hooks/useValue";
import useElementSize from "../../../hooks/useElementSize";
import { useEffect, useRef } from "react";
import axios from "../../../lib/axios/axios";
import TInput from "../TInput/TInput";
// import axios from "axios";

function TSelect({placeholder, initOptions, initZIndex, autocompleteAPI, autocompleteAPIParams}) {
    const selected = useValue(null);
    const isOpen = useValue(false);
    const options = useValue(initOptions ? initOptions : []);
    const [contentContainerRef, contentContainerSize] = useElementSize([options.value]);
    const selectContainerRef = useRef();

    useEffect(() => {
        if (autocompleteAPI) {
            axios.get(autocompleteAPI, {params: {
                ...autocompleteAPIParams
            }})
            .then(resp => {
                options.set(resp.data);
            })
        }
    }, [])

    function handleClickOutsideOnOpen(event) {
        if (selectContainerRef.current && !selectContainerRef.current.contains(event.target)) {
            onClose();
        }
    }

    const onClose = () => {
        isOpen.set(false);
    }

    const onOpen = () => {
        isOpen.set(true);
    }

    useEffect(() => {
        if (isOpen.value) {
            document.addEventListener("mousedown", handleClickOutsideOnOpen);
            return;
        }

        document.removeEventListener("mousedown", handleClickOutsideOnOpen);
        return () => {
            document.removeEventListener("mousedown", handleClickOutsideOnOpen);
        }
    }, [selectContainerRef, isOpen.value])

    const onToggle = () => {
        if (isOpen.value) {
            onClose();
            return;
        }

        onOpen();
    }

    const onSelect = (id) => {
        selected.set(id);
        onClose();
    }

    return (
        <div style={{...style.container, zIndex: initZIndex ? 10 + initZIndex : 10}} ref={selectContainerRef}>
            <Row justify="space-between" align="center" css={style.selectedContainer} onClick={onToggle}>
                <TText size={21} light>{selected.value !== null ? options.value.find(item => item.id === selected.value).name : " "}</TText>
                <IconChevronDown style={{transition: "300ms", transform: `rotate(${isOpen.value ? 180 : 0}deg)`}}/>
            </Row>
            <div style={{...style.contentContainer, height: isOpen.value ? contentContainerSize.height + "px" : 0}}>
                <div ref={contentContainerRef} style={style.contentWrapper}>
                    {autocompleteAPI && <TInput placeholder={"  "} light css={style.autocompleteInput}/>}
                    <div style={style.resultContainer}>
                        {options.value.map(option => (
                            <Row css={style.optionItem} onClick={() => onSelect(option.id)} key={option.id}>
                                <TText size={21} light>{option.name}</TText>
                            </Row>
                        ))}
                    </div>
                </div>
                
            </div>
            
        </div>
        
    )
}

const style = {
    autocompleteInput: {
        width: "100%",
    },
    container: {
        border: "2px solid",
        borderBottom: "none",
        padding: "5px",
        transition: "300ms",
        position: "relative",
        width: "100%"
    },
    selectedContainer: {
        cursor: "pointer",
    },
    contentContainer: {
        overflow: "hidden",
        transition: "300ms",
        border: "2px solid",
        borderTop: "none",
        position: "absolute",
        left: -2,
        width: "calc(100% + 4px)",
        top: "100%",
        background: "white",
    },
    resultContainer: {
        overflowY: "scroll",
        flexGrow: 1,
    },
    optionItem: {
        padding: "5px",
        cursor: "pointer",
        '&:hover': {
            background: "rgb(0, 0, 0, 0.1)"
        }
    },
    contentWrapper: {
        display: "flex",
        flexFlow: "column",
        maxHeight: "300px",
    }
}

export default TSelect;