import { Grid, Popover, Row } from "@nextui-org/react";
import TInput from "../TInput/TInput";
import useWindowSize from "../../../hooks/useWindowSize";
import { IconFilter, IconSearch } from "@tabler/icons-react";
import TText from "../TText";
import useValue from "../../../hooks/useValue";
import "./FilterController.css";
import TSelect from "../TSelect/TSelect";
import { API } from "../../../api/api";

function FilterController() {
    const windowSize = useWindowSize();
    const expandedControlPanel = useValue(false);

    const filterOptions = [
        {label: "", id: "age", options: [{name: "0+", id: 0}, {name: "3+", id: 3}, {name: "7+", id: 7}, {name: "12+", id: 12}, {name: "16+", id: 16}, {name: "18+", id: 18}]},
        {label: "", id: "genre", options: [{name: "", id: 1}, {name: "", id: 2}]},
        {label: "", id: "actor", autocompleteAPI: API.employees, autocompleteAPIParams: {category: "actor", limit: 10}}
    ]

    return (
        <Row css={style.controlContainer} className={`control-panel${expandedControlPanel.value ? " expanded-control-panel" : ""}`} align="center" fluid={false}>
            <TInput placeholder="..." css={style.input}
                    onFocus={() => expandedControlPanel.set(true)}
                    onBlur={() => expandedControlPanel.set(false)}
                    contentRight={<IconSearch stroke={3}/>}
                    className={"search-input"}
                    />
            {windowSize.width <= 650 &&
                <Popover placement="bottom-right" isBordered disableShadow offset={20}>
                    <Popover.Trigger>
                        <IconSearch className="icon-search"/>
                    </Popover.Trigger>
                    <Popover.Content css={{border: "none", padding: "10px"}}>
                        <TInput placeholder="..." css={style.popoverInput}
                        className={"search-input-popover"}
                        contentRight={<IconSearch stroke={3}/>}
                        />
                    </Popover.Content>
                </Popover>
            }
            <div style={style.filterButtonContainer}>
                <Popover placement="bottom-right" isBordered disableShadow offset={20}>
                    <Popover.Trigger>
                        <div>
                            <IconFilter className="icon-filter"/>
                            <TText css={style.filterButton} className={"text-filter"}></TText>
                        </div>
                    </Popover.Trigger>
                    <Popover.Content css={style.popoverFilterContent}>
                        <Grid.Container css={{w: "300px"}} gap={1}>
                            {filterOptions.map((item, idx, arr) => (
                                <>
                                    <Grid xs={4} alignItems="center">
                                        <TText size={24}>{item.label}</TText>
                                    </Grid>
                                    <Grid xs={8}>
                                        <TSelect initOptions={item.options} autocompleteAPI={item.autocompleteAPI} autocompleteAPIParams={item.autocompleteAPIParams} id={item.id} initZIndex={arr.length - idx}/>
                                    </Grid>
                                </>
                            ))}
                        </Grid.Container>
                    </Popover.Content>
                </Popover>
                <div style={style.filterButtonIndicator}/>
            </div>
        </Row>
    )
}

const style = {
    controlContainer: {
        paddingLeft: "15px",
        justifyContent: "flex-end",
        position: "relative",
        top: "3px",
        transition: "300ms",
    },
    filterButtonContainer: {
        position: "relative",
        cursor: "pointer",
    },
    filterButton: {
        fontSize: "27px",
        padding: "0 15px",
    },
    filterButtonIndicator: {
        width: "8px",
        height: "8px",
        borderRadius: "100%",
        background: "#8f2512",
        position: "absolute",
        top: 0,
        right: 0
    },
    inputContainer: {
        position: "relative",
    },
    input: {
        flexGrow: 1,
    },
    popoverInput: {
        width: "250px",
    },
    popoverFilterContent: {
        border: "2px solid",
        borderRadius: 0,
        padding: "15px",
        overflow: "visible",
    }
}

export default FilterController;