import { Col, Row, Text } from "@nextui-org/react";
import TText from "../../../Common/TText";
import moment from "moment";
import { TButton } from "../../../Common/TButton";

function NewsItemDesktop({news, last}) {

    return (
        <Row css={{...style.container, borderBottom: last ? "none" : "$primary solid 2px"}} className="news-item">
            <Col css={style.imageContainer}>
                <img src={news.img} style={style.image}/>
            </Col>
            <Col css={style.contentContainer}>
                <TText css={style.title} light className={"news-item__title"}>{news.title}</TText>
                <Row css={style.descriptionContainer}>
                    <Text css={style.description}>{news.description}</Text>
                </Row>
                <Row justify="space-between" align="center">
                    <TText css={style.date}>{moment(news.date).format("DD MMMM YYYY") }</TText>
                    <TButton><TText size={21} light>...</TText></TButton>
                </Row>
            </Col>
        </Row>
    );
}

