import TCardBase from "./TCardBase";
import TText from "./TText";
import "./TCard.css";
import useValue from "../../hooks/useValue";

function TCard({img, title, action, theme, light, classNameModificator}) {
    const isPressed = useValue(false);

    return (
        <div className={`tcard_main-page${isPressed.value ? " pressed " : ""}${classNameModificator ? ` ${classNameModificator} ` : ""}`}
            onMouseDown={() => isPressed.set(true)}
            onMouseUp={() => isPressed.set(false)}
            onMouseLeave={() => isPressed.set(false)}
            onTouchStart={() => isPressed.set(true)}
            onTouchEnd={() => isPressed.set(false)}
            >
            <div className="tcard_main-page__body">
                <img src={img}/>
            </div>
            <div className="tcard_main-page__footer">
                <TText light={light} css={{color: theme === "dark" ? "white" : "dark"}}>{title}</TText>
            </div>
        </div>
    )
    // return (
        // <TCardBase img={img} title={title} action={action} theme={theme} titleCSS={styles.title} footerCSS={styles.cardFooter} containerCSS={styles.card} className={"main-page-tcard"}/>
        // )
    }

export default TCard;