export function setMockEmployeesCategory(axiosMockAdapterInstance) {
    axiosMockAdapterInstance
    .onGet(API.categoriesOfEmployees)
    .reply(() => {
        return [
            200,
            [
                {id: 1, title: ""},
                {id: 2, title: " "},
                {id: 3, title: "  "},
            ]
        ]
    })
}

